var that;
import orderlist from '../order.js';
export default {
	data() {
		return {
			gid: '',
			goodslist: [],
			data: {},
			dzpay: false,
			payaction: -1,
			content: '',
			dzcard: [],
			modal: false,
			express: [],
			order_type: '',
			defaultData: {},
			showModel: false
		};
	},
	methods: {
		tpwl(e) {
			let detai_id = e.currentTarget.dataset.order_detai_id ? e.currentTarget.dataset.order_detai_id : '';
			uni.navigateTo({
				url: '/pages/order/shopOrderinfo/shopOrderinfo?id=' + that.data.order_id + '&detail_id=' + detai_id
			});
		},
		getinfo() {
			this.$request.request({
				url: that.api.order.detail,
				data: {
					order_id: that.gid,
					o_type: that.order_type
				},
				Toast: '正在加载...',
				success(res) {
					console.log('详情：', res);
					that.goodslist = res.goods_list;
					that.data = res;
				}
			});
		},
		sModel(e) {
			let detail = e.currentTarget.dataset,
				order_type = detail.ordertype;
			that.deleteId = detail.id;
			that.deleteType = order_type;
			that.deleteIndex = detail.index;
			that.showModel = true;
			that.defaultData = {
				title: '提示',
				content: '是否取消该订单',
				cancelText: '取消',
				confirmText: '确认',
				confirmColor: '#00ba00'
			};
		},
		onConfirm() {
			that.$request.request({
				url: that.api.order.revoke,
				data: {
					order_id: that.gid
				},
				Toast: '正在执行...',
				success(res) {
					that.$alertok('操作成功！');
					uni.navigateBack({
						delta: 1
					});
				},
				complete() {
					uni.hideLoading();
				}
			});
		}, 
		shouhuo(){ 
			that.$request.request({
				url: that.api.order.confirm,
				data: {
					order_id:that.gid
				},
				Toast: '操作中...',
				success(res) {
					that.$alertok('已确认收货');
					setTimeout(function() {
						that.getinfo();
					}, 1000);
				},
				complete(){
					uni.hideLoading();
				}
			});
		}, 
		orderRevoke: function(e) {
			var a = this;
			uni.showModal({
				title: '提示',
				content: '是否取消该订单',
				cancelText: '否',
				confirmText: '是',
				success: function(t) {
					if (t.confirm) {
						that.$request.request({
							url: that.api.order.revoke,
							data: {
								order_id: that.gid
							},
							Toast: '正在执行',
							success() {
								that.$alertok('操作成功！');
								uni.navigateBack({
									delta: 1
								});
							},
							fail(res) {
								uni.hideLoading();
							}
						});
					}
				}
			});
		},
		// 选择普通支付方式
		getpaytype(e) {
			if (that.payaction == e.currentTarget.dataset.index) {
				that.payaction = -1;
			} else {
				that.payaction = e.currentTarget.dataset.index;
			}
			that.$forceUpdate();
		},
		// 控制弹窗
		hideModal(e) {
			that.modal = !that.modal;
		},
		shouh(e) {
			console.log(e.currentTarget.dataset);
			uni.navigateTo({
				url: '/pages/order/refundOrder/refundOrder?id=' + e.currentTarget.dataset.order_detai_id
			});
		},
		// 支付
		pay() {
			let id = that.gid;
			let order_type = that.ordertype;
			if (order_type) {
				that.$request
					.request({
						url: that.api.live_order.pay_data,
						data: {
							order_id: id
						},
						method: 'post'
					})
					.then(res => {});
			} else {
				that.$pay
					.pay(0, {
						order_id: id,
						order_type: order_type
					})
					.then(res => {});
			}
		},
		getwl(orderinfo) {
			let order_type = that.order_type;
			this.$request.request({
				url: that.api.order.express_detail,
				data: {
					order_id: that.gid,
					detail_id: that.detail_id,
					o_type: order_type
				},
				rejectType: true,
				success(res) {
					that.express = res.list ? res.list : [];
				}
			});
		}
	},
	onLoad(option) {
		that = this;
		that.gid = option.id ? option.id : '21';
		that.order_type = option.order_type ? option.order_type : '';
	},
	onShow() {
		that.userinfo = uni.getStorageSync('userinfo');
		that.getinfo();
		that.getwl();
	}
};
